.about__wrap {
    padding: 20px 50px;
}
.about {
}
.about__photo-text {
    display: flex;
    gap: 20px;
    align-items: center;
}
.about__text {
    max-width: 580px;
    max-height: max-content;
    display: grid;
    gap: 24px;

    h2 {
        font-size: px-to-rem(48px);
        line-height: px-to-rem(60px);
        font-weight: 500;
    }

    ul {
        max-width: 440px;
    }

    li {
        list-style: none;
        font-size: px-to-rem(18px);
        line-height: px-to-rem(27px);
        margin-bottom: 20px;
        list-style-image: url("../img/dot.svg");
        margin-left: 20px;
    }
}

.about__photo {
    max-width: 582px;
}

@media (max-width:1430px) { 

    .about__text {
        gap: 25px;
    
        h2 {
            font-size: px-to-rem(36px);
            line-height: px-to-rem(40px);
        }
    
        .btn {
            padding: 20px 30px;
        }
    }

    .about__photo {
        max-width: 450px;
    }

}

@media (max-width:900px) { 

    .about__photo {
        max-width: 350px;
    }

    .about__text {
    
        h2 {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(30px);
        }

        li {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(22px);
        }

        .btn {
            font-size: px-to-rem(16px);
            padding: 15px 20px;
        }
    }

}

@media (max-width:780px) { 


    .about__photo-text {

        gap: 0;
        flex-direction: column;
        align-items: center;

        .about__text {
            padding-bottom: 0;
            text-align: center;
            padding: 0;

            ul {
                max-width: 100%;
            }

            .subtitle {
                margin: 0 auto;
            }

            .btn {
                margin: 0 auto;
            }
        }

        .about__photo {
            padding-top: 30px;
        }
    
    }

}

@media (max-width:400px) { 

    .about__wrap {
        padding: 20px 0;
    }

    .about__text {
        max-width: 270px;
    
        h2 {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(26px);
        }
    
    }

    .about__photo {
        width: 100%;
    }
}