.cakes__wrap {
    padding-bottom: 30px;
    position: relative;
}

.section__title {
    text-align: center;
    padding-bottom: 80px;

    h2 {
        font-weight: 500;
        font-size: px-to-rem(48px);
        line-height: px-to-rem(60px);
    }
}

.cakes__block {
    display: grid;
    grid-template-columns: auto auto auto;
    gap: 20px;
}

.cakes__item {
    border: 1px solid $gray;
    border-radius: 10px;

    img {
        height: 245px;
    }

    &:hover {
        box-shadow: 2px 8px 16px rgba(194, 198, 208, 0.15);
    }
}

.cakes__text {
    padding: 50px 40px;
    display: grid;
    gap: 14px;

    h3 {
        font-weight: 700;
        font-size: px-to-rem(24px);
        line-height: px-to-rem(34px);
    }

    .price {
        font-weight: 500;
        font-size: px-to-rem(24px);
        line-height: px-to-rem(36px);
    }

    .btn {
        padding: 20px 40px;
    }
}

.cakes__berry-left {
    position: absolute;
    top: 10%;
    left: 0;
}

.cakes__leaf-right {
    position: absolute;
    top: 30%;
    right: 0;
}

.cakes__leaf-left {
    position: absolute;
    bottom: 20%;
    left: 0;
}

@media (max-width:1200px) {

    .section__title {
    
        h2 {
            font-size: px-to-rem(36px);
            line-height: px-to-rem(50px);
            max-width: 560px;
            margin: 0 auto;
        }
    }

    .cakes__block {
        grid-template-columns: 310px 310px;
        justify-content: space-evenly;
    }

    .cakes__text {
        padding: 30px 30px;
    
        h3 {
            font-size: px-to-rem(22px);
            line-height: px-to-rem(28px);
        }
    
        .price {
            font-size: px-to-rem(22px);
            line-height: px-to-rem(28px);
        }
    
        .btn {
            padding: 15px 25px;
        }
    }
}

@media (max-width: 668px) {

    .section__title {
        padding-top: 20px;
        padding-bottom: 40px;
    
        h2 {
            font-size: px-to-rem(26px);
            line-height: px-to-rem(30px);
            max-width: 300px;
            margin: 0 auto;
        }
    }

    .cakes__block {
        grid-template-columns: 310px;
        justify-content: center;
    }
}

@media (max-width:400px) { 

    .section__title {
    
        h2 {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(26px);
        }
    
    }

    .cakes__text {
        padding: 30px 30px;
    
        h3 {
            font-size: px-to-rem(18px);
            line-height: px-to-rem(24px);
        }
    
        .price {
            font-size: px-to-rem(18px);
            line-height: px-to-rem(24px);
        }

        .btn {
            font-size: px-to-rem(16px);
        }
    
    }
}
