.offer {
    padding-top: 188px;
    padding-bottom: 300px;
    display: grid;
    flex-wrap: wrap;
    gap: 40px;
}

.offer__text {
    display: grid;
    flex-wrap: wrap;
    gap: 24px;
    max-width: 650px;
    justify-content: left;

    h1 {
        font-size: px-to-rem(48px);
        line-height: px-to-rem(60px);
        font-weight: 700;
    }

    .offer__text-excerpt {
        max-width: 500px;
        font-size: px-to-rem(24px);
        line-height: px-to-rem(34px);
    }
}

.offer__btn {
    width: max-content;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    align-items:center;

    p {
        width: 150px;
        font-size: px-to-rem(15px);
    }
}

.offer__berry-right {
    position: absolute;
    top: 10%;
    right: 0;
    width: 170px;
}

.offer__berry-left {
    position: absolute;
    top: 30%;
    left: 0;
    width: 195px;
}

.offer__berry-right-bottom {
    position: absolute;
    bottom: 10%;
    right: 10%;
    width: 195px;
}

.offer__leaf {
    position: absolute;
    bottom: -5%;
    left: 0;
    width: 275px;
}

.offer__cload {
    display: none;
}


@media (min-width:1600px) {
    .header__wrap {
        padding-bottom: 10%;
    }
}

@media (min-width:1900px) {
    .header__wrap {
        padding-bottom: 18%;
    }
}

@media (min-width:2400px) {
    .header__wrap {
        padding-bottom: 25%;
    }
    .offer__berry-right {
        right: 10%;
    }

    .offer__berry-left {
        left: 10%;
    }

    .offer__berry-right-bottom {
        bottom: 15%;
        right: 20%;
    }

    .offer__leaf {
        left: 15%;
    }
}


@media (max-width:1430px) { 

    .header__wrap {
        padding-bottom: 5%;
    }

    .offer {
        padding: 150px 50px 250px;
        gap: 25px;
    }

    .offer__text {
        max-width: 500px;
        gap: 25px;
    
        h1 {
            font-size: px-to-rem(36px);
            line-height: px-to-rem(40px);
        }
    
        .offer__text-excerpt {
            max-width: 500px;
            font-size: px-to-rem(18px);
            line-height: px-to-rem(26px);
        }
    }

    .offer__btn {

        .btn {
            padding: 20px 30px;
        }
    }

    .offer__berry-right {
        top: 15%;
        right: 10%;
        width: 100px;
    }
    
    .offer__berry-left {
        top: 60%;
        left: 40%;
        width: 130px;
    }
    
    .offer__berry-right-bottom {
        bottom: 15%;
        right: 15%;
        width: 130px;
    }
    
    .offer__leaf {
        bottom: -5%;
        left: 0;
        width: 180px;
    }

}

@media (max-width: 1200px) {
    .offer {
        padding: 150px 50px 150px;
    }
}

@media (max-width:1100px) {

    .header__wrap {
        background-size: 150%;
        background-position-x: -80px;
    } 
    .offer {
        padding: 150px 50px 350px;
    }
}

@media (max-width:800px) {

    .header__wrap {
        background-size: 200%;
        background-position-x: -300px;
    }
}

@media (max-width:700px) {

    .offer {
        padding: 100px 50px 250px;
    }
    
    .offer__text {
        max-width: 330px;
    
        h1 {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(30px);
        }
    
        .offer__text-excerpt {
            max-width: 350px;
            font-size: px-to-rem(16px);
            line-height: px-to-rem(22px);
        }
    }

    .offer__btn {
        display: grid;

        .btn {
            font-size: px-to-rem(16px);
            padding: 15px 20px;
            margin: 0 auto;
        }
    }
}

@media (max-width:630px) {

    .header__wrap {
        padding-bottom: 0;
    }

    .header__wrap {
        background-image: none;
        background-color: $bg-color;
    }

    .img__off {
        display: none;
    }

    .offer {
        padding: 60px 20px 0;
        text-align: center;
        justify-content: center;

        .subtitle {
            margin: 0 auto;
        }
    }

    .offer__btn {
        margin: 0 auto;
        p {
            width: max-content;
        }
    }

    .offer__cload {
        display: block;
        border: none;
    }
}

@media (max-width:400px) {

    .offer__text {
        max-width: 270px;
    
        h1 {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(26px);
        }
    
    }
}



