.promo__wrap {
    background-color: $bg-color;
    color: $white;
    position: relative;
}

.promo__cload-top {
    position: absolute;
    top: 0;
    width: 100%;
    height: 30%;
}

.promo__cload-bottom {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 30%;
}

.promo__photo-text {
    display: flex;
    gap: 80px;
    padding-top: 200px;
    justify-content: center;

    .promo__photo {
        width: 600px;
        object-fit: contain;
    }
}

.promo__text {
    display: grid;
    gap: 24px;
    align-items: center;
    height: max-content;
    max-width: 500px;
    margin: auto 0;
    padding-bottom: 200px;

    h2 {
        font-size: px-to-rem(48px);
        line-height: px-to-rem(60px);
        font-weight: 500;
    }

    .promo__text-excerpt {
        font-size: px-to-rem(21px);
        line-height: px-to-rem(30px);
    }
}

.promo__berry-right {
    position: absolute;
    top: 0;
    right: 0;
}

.promo__leaf {
    position: absolute;
    bottom: 30%;
    left: 0;
}


@media (max-width:1430px) { 

    .promo {
        padding-right: 50px;
    }

    .promo__text {
        gap: 25px;
    
        h2 {
            font-size: px-to-rem(36px);
            line-height: px-to-rem(40px);
        }
    
        .promo__text-excerpt {
            font-size: px-to-rem(18px);
            line-height: px-to-rem(26px);
        }

        .btn {
            padding: 20px 30px;
        }
    }

}

@media (max-width:1130px) { 

    .promo__photo-text {
        justify-content: space-between;

        .promo__photo {
            width: 400px;
            padding-top: 50px;
        }
    
    }

}

@media (max-width:900px) { 

    .promo__photo-text {
        gap: 40px;
    }    

    .promo__text {
    
        h2 {
            font-size: px-to-rem(24px);
            line-height: px-to-rem(30px);
        }
    
        .promo__text-excerpt {
            font-size: px-to-rem(16px);
            line-height: px-to-rem(22px);
        }

        .btn {
            font-size: px-to-rem(16px);
            padding: 15px 20px;
        }
    }

}

@media (max-width:780px) { 

    .promo {
        padding-right: 0;
    }

    .promo__photo-text {

        padding-top: 100px;
        gap: 0;
        flex-direction: column-reverse;
        align-items: center;

        .promo__photo {
            padding-top: 0;
        }

        .promo__text {
            padding-bottom: 0;
            text-align: center;
            padding: 20px;

            .subtitle {
                margin: 0 auto;
            }

            .btn {
                margin: 0 auto;
            }
        }
    
    }

    .promo__cload-bottom, .promo__cload-top {
        height: auto;
    }

}

@media (max-width:400px) { 

    .promo__text {
        max-width: 270px;
    
        h2 {
            font-size: px-to-rem(20px);
            line-height: px-to-rem(26px);
        }
    
    }
}
