@charset "UTF-8";
/* Указываем box sizing */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внутренние отступы */
ul[class],
ol[class] {
  padding: 0;
}

/* Убираем внешние отступы */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
  padding: 0;
}

/* Выставляем основные настройки по-умолчанию для body */
body {
  min-height: 100vh;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.3;
}

/* Удаляем стандартную стилизацию для всех ul и il, у которых есть атрибут class*/
ul[class],
ol[class] {
  list-style: none;
}

/* Упрощаем работу с изображениями */
img {
  max-width: 100%;
  display: block;
}

/* Наследуем шрифты для инпутов и кнопок */
input,
button,
textarea,
select {
  font: inherit;
}

/**
 * Основные стили
 **/
html {
  font-size: 16px;
  color: #000;
}

body {
  font-family: "Montserrat", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
  font-weight: 400;
}

a {
  text-decoration: none;
}

.btn {
  text-decoration: none;
  box-sizing: border-box;
  text-align: center;
  color: #2E363E;
  border-radius: 8px;
  font-weight: 500;
  width: max-content;
  padding: 30px 50px;
  font-size: 1.125rem;
  display: inline-block;
  background-color: #FED84C;
}
.btn:hover {
  background-color: #e3b301;
  text-decoration: none;
}

.subtitle {
  background-color: #F2F6FA;
  width: max-content;
  color: #2E363E;
  font-size: 0.6875rem;
  padding: 10px 18px;
  border-radius: 5px;
  cursor: default;
}

.container {
  min-width: 320px;
  max-width: 100%;
  padding: 0 0;
  box-sizing: border-box;
  margin: 0 auto;
}

.wrap {
  min-width: 320px;
  max-width: 1170px;
  padding: 0 15px;
  box-sizing: border-box;
  margin: 0 auto;
}

.header__wrap {
  background-color: #fff;
  width: 100%;
  color: #fff;
  background-image: url("../img/bg.jpg");
  background-repeat: no-repeat;
  background-size: 100%;
  position: relative;
}
.header__wrap hr {
  margin: 0;
  border: 0.1px solid #7E868E;
  opacity: 0.1;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 10px;
}
.header a {
  color: white;
}

.header__logo {
  font-size: 1.25rem;
}

.header__contact {
  display: flex;
  flex-wrap: wrap;
  gap: 60px;
  align-items: center;
}

.header__map {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
}
.header__map .header__map-img {
  height: 17px;
}
.header__map p {
  font-size: 0.75rem;
  max-width: 120px;
}

.header__phone {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 12px;
}
.header__phone a {
  font-size: 1.25rem;
  font-weight: 700;
}
.header__phone p {
  font-size: 0.8125rem;
}

@media (max-width: 700px) {
  .header {
    flex-direction: column;
    row-gap: 20px;
  }
}
@media (max-width: 500px) {
  .header__contact {
    flex-direction: column;
    row-gap: 20px;
  }
}
.offer {
  padding-top: 188px;
  padding-bottom: 300px;
  display: grid;
  flex-wrap: wrap;
  gap: 40px;
}

.offer__text {
  display: grid;
  flex-wrap: wrap;
  gap: 24px;
  max-width: 650px;
  justify-content: left;
}
.offer__text h1 {
  font-size: 3rem;
  line-height: 3.75rem;
  font-weight: 700;
}
.offer__text .offer__text-excerpt {
  max-width: 500px;
  font-size: 1.5rem;
  line-height: 2.125rem;
}

.offer__btn {
  width: max-content;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
}
.offer__btn p {
  width: 150px;
  font-size: 0.9375rem;
}

.offer__berry-right {
  position: absolute;
  top: 10%;
  right: 0;
  width: 170px;
}

.offer__berry-left {
  position: absolute;
  top: 30%;
  left: 0;
  width: 195px;
}

.offer__berry-right-bottom {
  position: absolute;
  bottom: 10%;
  right: 10%;
  width: 195px;
}

.offer__leaf {
  position: absolute;
  bottom: -5%;
  left: 0;
  width: 275px;
}

.offer__cload {
  display: none;
}

@media (min-width: 1600px) {
  .header__wrap {
    padding-bottom: 10%;
