.header__wrap {
    background-color: $white;
    width: 100%;
    color: $white;
    background-image: url("../img/bg.jpg");
    background-repeat: no-repeat;
    background-size: 100%;
    position: relative;

    hr {
        margin: 0;
        border: 0.1px solid #7E868E;
        opacity: 0.1;
    }

}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 30px 10px;
    
    a {
        color: white;
    }
}

.header__logo {
    font-size: px-to-rem(20px);
}

.header__contact {
    display: flex;
    flex-wrap: wrap;
    gap: 60px;
    align-items: center;
}

.header__map {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px;

    .header__map-img {
        height: 17px;
    }

    p {
        font-size: px-to-rem(12px);
        max-width: 120px;
    }
}

.header__phone {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 12px;

    a {
        font-size: px-to-rem(20px);
        font-weight: 700;
    }
    p {
        font-size: px-to-rem(13px);
    }
}


@media (max-width:700px) {

    .header {
        flex-direction: column;
        row-gap: 20px;
    }
    
}

@media (max-width:500px) {

    .header__contact {
        flex-direction: column;
        row-gap: 20px;
    }
    
}